body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-inter: Inter;
  --font-roboto: Roboto;
  --font-lato: Lato;

  /* font sizes */
  --font-size-mini: 15px;
  --font-size-3xs: 10px;
  --font-size-29xl: 48px;
  --font-size-19xl: 38px;
  --font-size-10xl: 29px;

  /* Colors */
  --color-gray-100: #0a1a40;
  --color-gray-200: rgba(255, 255, 255, 0.08);
  --color-darkslateblue: #1c2a4d;
  --color-linen-100: #f6eae1;
  --color-black: #000;
  --color-whitesmoke: rgba(245, 245, 245, 0.7);
  --color-cornsilk: #f7f2d6;

  /* Gaps */
  --gap-81xl: 100px;
  --gap-0: 0px;
  --gap-11xl: 30px;
  --gap-23xl: 42px;

  /* Paddings */
  --padding-3xs: 10px;
  --padding-2xl: 21px;
  --padding-xl: 20px;
  --padding-21xl: 40px;
  --padding-sm: 14px;
  --padding-12xs: 1px;
  --padding-8xs: 5px;
  --padding-11xs: 2px;
  --padding-8xl: 27px;
  --padding-9xs: 4px;
  --padding-10xs: 3px;
  --padding-7xs: 6px;
  --padding-mini: 15px;
  --padding-2xs: 11px;
  --padding-smi: 13px;
  --padding-94xl: 113px;
  --padding-mid: 17px;
}
